<template>
    <div class="yingyong">
        <headerBar :title="headerNavData"></headerBar>
        <yd-flexbox class="yd-cell-item">
            <yd-flexbox-item>
              <yd-input style="width: 100%;height: 1rem;font-size: .3rem;padding-left: .12rem" readonly type="text" v-model="codedata.addr" :placeholder="codedata.addr" :show-clear-icon="false" :show-success-icon="false"></yd-input>
            </yd-flexbox-item>
        </yd-flexbox>
        <yd-cell-group>
            <yd-cell-item arrow>
                <span slot="left">{{$t('data.Selectcurrency')}}：</span>
                <input slot="right" type="text" @click.stop="showcoin = true" v-model="coinName" readonly :placeholder="$t('data.Pleaseselectcurrency')">
            </yd-cell-item>
            <yd-cell-item>
                <span slot="left"></span>
                <yd-input slot="right" type="number" v-model="codedata.num" :show-clear-icon="false" :show-success-icon="false" :show-required-icon="false" :placeholder="$t('data.topupnumber')"></yd-input>
            </yd-cell-item>
            <yd-cell-item>
                <span slot="left"></span>
                <yd-input slot="right" type="text" v-model="codedata.phone" :show-clear-icon="false" :show-success-icon="false" :show-required-icon="false" :placeholder="$t('data.impulseaccount')"></yd-input>
            </yd-cell-item>
            <yd-cell-item>
                <span slot="left"></span>
                <yd-input slot="right" type="text" v-model="codedata.beizhu" :show-clear-icon="false" :show-success-icon="false" :show-required-icon="false" :placeholder="$t('placeholder.codemome')"></yd-input>
            </yd-cell-item>
        </yd-cell-group>
        <div class="block">
          <yd-flexbox class="ttle">
            <yd-flexbox-item>
              {{$t('code.kgf')}}
            </yd-flexbox-item>
            <!-- <div class="wenhao"><img src="../assets/images/w.png"></div> -->
          </yd-flexbox>
          <el-slider v-model="num" :min="min" :max="max" :step="step" :show-tooltip="false"></el-slider>
          <yd-flexbox class="qml">
            <div class="man">{{$t('data.slow')}}</div>
            <yd-flexbox-item>
              <div class="kgf">{{num/10000 | DAD}} ether</div>
            </yd-flexbox-item>
            <div class="kuai">{{$t('data.fast')}}</div>
          </yd-flexbox>
          <button type="button" class="btn_c" 
          @click="codedata.num && codedata.phone&&showpwd()" 
          :class="{'btn_c_' : codedata.num && codedata.phone}">{{$t('data.toup')}}</button>
        </div>
        <!-- 下拉框 -->
        <yd-popup v-model="showcoin" position="bottom" height="30%">
            <p class="select_p" @click.stop="result1('ETH')">ETH</p>
            <!-- <p class="select_p" @click.stop="result1('BDK')">BDK</p> -->
        </yd-popup>
        <!-- 密码输入框 -->
        <yd-popup v-model="show1" position="center" width="90%">
            <div class="dig">
                <p class="ttl">
                    {{padtxt}}
                </p>
                <input type="password" name="" v-model="pawd">
                <p class="dig_bt">
                    <yd-button @click.native="show1 = false">{{$t('code.cancel')}}</yd-button>
                    <yd-button @click.native="trade()">{{$t('code.ok')}}</yd-button>
                </p>
            </div>
        </yd-popup>
    </div>
  
</template>

<script>
  import { getTokenQty,price,MysendRawTransaction,getPrivateFun,ToZhuanZhang,Setvuefghfvmdfgdfgdfg,Getvuefghfvmdfgdfgdfg,GetGasPrice,encrypt,MysendRawTX } from '../assets/js/Keytoken.js'
  import {getCookie,setCookie} from '../assets/js/cookie.js'
  import headerBar from './header.vue'
export default {
  components:{
      "headerBar":headerBar
  },
  data () {
    return {
      codedata:{
        num:'',
        addr:'',
        beizhu:'',
        phone:'',
        showpassword:'',
        check:[1]
      },
      value1:1,
      min:0,
      max:0,
      step:0,
      pawd:'',
      show1:false,
      num:0,
      skey:'',
      padtxt:this.$t('code.rupassword'),
      password:'',
      headerNavData:{
         left:true,
         title:this.$t('data.ImpulseTopUp'),
         right:false,
         bj:'#2c94ed',
         search:false,
         share:false
      },
      showcoin: false,
      coinName: 'ETH',
      iframesrc:'',
      iframeshow:false,
      gas:200000
    }
  },
  created(){
    this.closeloading()
    this.codedata.addr = localStorage.getItem("toAddress")
    this.get_gas_price()
  },
  methods:{
    get_gas_price(){
      var that = this
      this.axios({
          url:this.Url + 'EthCommond.ashx',
          method:'post',
          params: {cmd:"gasprice"}
      }).then(res => {
        if(res.data.success){
          // var gasprice=(GetGasPrice(1,res.data.data));
          // var totalGasPrice = gasprice * that.gas
          // that.min =parseFloat(totalGasPrice.toFixed(8));
          // that.max = parseFloat((parseFloat(this.min*10).toFixed(8)));
          // that.step =parseFloat((parseFloat((this.max - this.min)/20).toFixed(8)));
          // that.num = this.min
          var gasprice = res.data.data*10000;
          var totalGasPrice = gasprice * that.gas;
          that.min = totalGasPrice;
          that.max = that.min * 10;
          that.step = that.max / 20;
          that.num = totalGasPrice;
        }
      })
    },
    result1(ret) {
        this.coinName = ret;
        this.showcoin=false
    },
    // 转账交易
    trade(){
      var that = this
      this.show1 = false
      this.openloading(this.$t('data.topuping'))
      var name = this.coinName
      var codelist = JSON.parse(getCookie("codelist"))
      var publickey = getCookie("publickey")
      var contract = this.$route.query.contractAddress
      for (var i = 0; i < codelist.length; i++) {
        if (publickey == codelist[i].publickey) {
            var keystore = codelist[i].keystore;
            getPrivateFun(keystore,that.pawd,function(skey){
                if(!skey||skey.length!=66)
                 {
                  that.closeloading()
                  that.padtxt=that.$t('data.Passworderror')
                  setTimeout(function(){
                     that.show1 = true
                   },2000)
                }
                var num = that.codedata.num
                var gasprice = (that.num / (that.gas * 10000)).toFixed(18);
                var numprice = GetGasPrice(0,gasprice)
                var you=  that.codedata.addr
                // 以太坊交易
                 MysendRawTX(name,contract,publickey,you,skey,num,that.gas,numprice,that.codedata.phone,function(data){
                  if(!data.success){
                    that.padtxt=data.msg
                    return;
                  }
                  that.axios({
                    url:that.Url + 'ETHCommond.ashx',
                    method:'post',
                    headers:{'Content-Type':'application/x-www-form-urlencoded'},
                    params: {cmd:"send_raw_transaction",publickey:getCookie("publickey"),tx:data.data,value:num,symbol:name,remark:that.codedata.beizhu,from:publickey,to:you,nonce:data.nonce}
                  }).then(res=>{
                    that.closeloading()
                    if(res.data.success){
                      that.$dialog.alert({mes: that.$t('data.Rechargesuccessful')});
                      // that.Toast("充值成功等待系统确认")
                    }else{
                      that.$dialog.alert({mes: res.data.msg});
                      // that.Toast("充值失败")
                    }
                  })
                })
            });
        }
      }
    },
    showpwd(){
      var reg = /^1[3|4|5|6|7|8|9][0-9]\d{4,8}$/
      var phone = this.codedata.phone
      if(!reg.test(phone)){
        this.Toast(this.$t('data.correctphonenumber'))
        return;
      }
      this.show1 = true
    },
    gowritezjc(){
      this.show1 = false
      this.$router.push({path:'/writezjc'})
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.yingyong{
  padding-top: .9rem;
  font-family: -apple-system, BlinkMacSystemFont, "PingFang SC","Helvetica Neue",STHeiti,"Microsoft Yahei",Arial,Tahoma,Simsun,sans-serif;
}
.yd-cell-item{
  border-bottom: 1px solid #f4f4f4;
}
.yd-checkbox-text{
  display: none;
}
.reda{
  padding: 0 0 0 .28rem;
  font-size: .26rem;
  color: #666;
}
.reda span{
  color: #278eed
}
.dig{
  background: #fff;
  padding: .4rem;
  text-align: center;
}
.ttl{
  font-size: .3rem;
  color: #278eed;
}
.dig input{
  width: 100%;
  height: .7rem;
  line-height: .7rem;
  padding: 0 .12rem;
  background: #f4f4f4;
  border:none;
  border-radius: 5px;
  color: #999;
  font-size: .28rem;
  margin: .3rem 0;
}
.dig_bt button{
  margin: 0 .3rem;
  padding: 0 .5rem
}
.dig_bt button:nth-child(1){
  background: #c20919;
}
.dig_bt button:nth-child(2){
  background: #278eed;
}
.btn_c{
  margin: 1.35rem 0 .15rem;
  height: .7rem;
  line-height: .7rem;
  text-align: center;
  width: 100%;
  border: none;
  background: #f4f4f4;
  font-size: .28rem;
  color: #fff;
  border-radius: 5px;
}
.btn_c_{
    background: #278eed!important;
}
.createf{
  background: #f4f4f4;
  padding: .2rem .35rem; 
  font-size: .26rem;
  color: #278eed
}
.touxi img{
  width: .5rem
}
.block{
  padding: 0 .3rem
}
.qml{
  text-align: center;
  margin-top: .2rem
}
.qml .man{
  font-size: .28rem;
  color: #666;
  text-align: left;
}
.qml .kuai{
  font-size: .28rem;
  color: #666;
  text-align: right;
}
.kgf{
  font-size: .25rem;
  color: #888
}
.ttle{
  font-size: .28rem;
  color: #888;
  margin-bottom: .15rem
}
.ttle img{
  width: .4rem;
}
.select_p{
  height: .9rem;
  text-align: center;
  border-bottom: 1px solid #f1f1f1;
  line-height: .9rem;
  font-size: .36rem
}
#show-iframe{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #fff;
  z-index: 9999;
}
</style>



